import { useEffect } from 'react';
import { connect } from 'react-redux';
import { setConfig, setTodos } from '../../redux/actions'
import './Home.css';

const mapStateToProps = (state) => {
    return {
        config: state.config,
        todos: state.todos
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        changeConfig: (cfg) => dispatch(setConfig(cfg)),
        changeTodos: (todos) => dispatch(setTodos(todos))
    }
}

function Home({config, todos, changeConfig, changeTodos}) {
    document.body.style.background = "linear-gradient(0deg, #ad00ff 1%, rgba(255,255,255,1) 7%, rgba(255,255,255,1) 93%, #ad00ff 100%)"

    useEffect(() => { // Load data
        let i = localStorage.getItem('todos');
        if (i) {
            changeTodos(JSON.parse(i))
        }
    }, [changeTodos])

    useEffect(() => { // Save data
        localStorage.setItem('todos', JSON.stringify(todos))
    }, [todos])

    let magicFn = (screenId, className) => {
        let cfg = {...config};
        let arr = cfg.cn.split(' ');
        cfg.cn = arr.length > 1 ? arr[0] : cfg.cn
        cfg.cn = cfg.cn + ' fadeOut'
        changeConfig(cfg);
        setTimeout(() => {
            cfg = {...config}
            cfg.screen = screenId
            cfg.cn = className + ' fadeIn'
            changeConfig(cfg);
        }, 480)
    }

    var changeScreen = (e, id) => {
        switch (id) {
            case 0: { // Cambio de home a main
                magicFn(1, "mainCard")
                break;
            }
            case 1: { // Cambio de main a home
                magicFn(0, "homeCard")
                break;
            }
            case 2: { // Todo list
                magicFn(2, "todoCard")
                break;
            }
            case 3: { // Doing list
                magicFn(3, "todoCard")
                break;
            }
            case 4: { // Done list
                magicFn(4, "todoCard")
                break;
            }
            case 5: { // formulario creación
                magicFn(5, "formAdd")
                break;
            }
            case 6: { // formulario creación
                magicFn(6, "mainCard")
                break;
            }
            case 7: { // detalles y opciones
                magicFn(7, "detailCard")
                break;
            }

            default: return;
        }
    }

    useEffect((cs = changeScreen) => { // change cfg info
        if (config.info.id) cs(null, 7)
    }, [config.info]) // eslint-disable-line react-hooks/exhaustive-deps

    let addTodo = (id) => {
        switch (id) {
            case 0: {
                changeTodos({...todos, todo: [{title: config.add.title, description: config.add.description}, ...todos.todo]})
                break;
            } case 1: {
                changeTodos({...todos, doing: [{title: config.add.title, description: config.add.description}, ...todos.doing]})
                break;
            } case 2: {
                changeTodos({...todos, done: [{title: config.add.title, description: config.add.description}, ...todos.done]})
                break;
            }
            default: return;
        }
        let cfg = {...config};
        cfg.add.title = '';
        cfg.add.description = '';
        changeConfig(cfg);
        setTimeout(() => changeScreen(null, id+2), 1)
    }

    let handleInfo = (id, i) => {
        let cfg = {...config};
        cfg.info = {id: id, i: i};
        changeConfig(cfg);
    }

    let handleBtn = (id) => {
        switch (id) {
            case -1: { // remove
                let t = {...todos};
                t[config.info.id].splice(config.info.i, 1)
                changeTodos(t);
                changeScreen(null, config.info.id === 'todo' ? 2 : config.info.id === 'doing' ? 3 : 4)
                break;
            } case 0: { // move to todo
                let t = {...todos};
                let move = t[config.info.id].splice(config.info.i, 1)[0]
                t['todo'] = [move, ...t.todo]
                changeTodos(t);
                changeScreen(null, 0)
                break;
            } case 1: { // move to doing
                let t = {...todos};
                let move = t[config.info.id].splice(config.info.i, 1)[0]
                t['doing'] = [move, ...t.doing]
                changeTodos(t);
                changeScreen(null, 0)
                break;
            } case 2: { // move to done
                let t = {...todos};
                let move = t[config.info.id].splice(config.info.i, 1)[0]
                t['done'] = [move, ...t.done]
                changeTodos(t);
                changeScreen(null, 0)
                break;
            } default: return;
        }
    }

    if (!config.screen) { // Home
        return (<div className="homeContainer">
            <div className={config.cn}>
                <h1>Todo List <span>APP</span></h1>
                <div id="enterBtn" onClick={e => changeScreen(e, 0)}>Enter</div>
            </div>
        </div>)
    } else {
        switch (config.screen) {
            // Main
            case 1: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 2)} className="chooseCard">Todos<span>({todos.todo.length})</span></div>
                    <div onClick={e => changeScreen(e, 3)} className="chooseCard">Doing<span>({todos.doing.length})</span></div>
                    <div onClick={e => changeScreen(e, 4)} className="chooseCard">Done<span>({todos.done.length})</span></div>
                    <div onClick={e => changeScreen(e, 5)} className="chooseCard">Add</div>
                </div>
            </div>)
            // Todos
            case 2: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 0)} className="quit" ><b>X</b></div>
                    {(() => {
                        if (!todos.todo.length) return <h1>There is nothing to show</h1>
                        else return todos.todo.map((obj, i) => <div key={i} onClick={e => handleInfo('todo', i)} className="chooseCard">{obj.title}</div>)
                    })()}
                </div>
            </div>)
            // Doing
            case 3: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 0)} className="quit" ><b>X</b></div>
                    {(() => {
                        if (!todos.doing.length) return <h1>There is nothing to show</h1>
                        else return todos.doing.map((obj, i) => <div key={i} onClick={e => handleInfo('doing', i)} className="chooseCard">{obj.title}</div>)
                    })()}
                </div>
            </div>)
            // Done
            case 4: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 0)} className="quit" ><b>X</b></div>
                    {(() => {
                        if (!todos.done.length) return <h1>There is nothing to show</h1>
                        else return todos.done.map((obj, i) => <div key={i} onClick={e => handleInfo('done', i)} className="chooseCard">{obj.title}</div>)
                    })()}
                </div>
            </div>)
            // Form
            case 5: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 0)} className="quit" ><b>X</b></div>
                    <form onSubmit={e => {
                        e.preventDefault()
                        changeScreen(e, 6)
                    }}>
                        <input type="text" value={config.add.title} onChange={e => changeConfig({...config, add: {...config.add, title: e.target.value}})} name="title" placeholder="Title"/>
                        <input type="text" value={config.add.description} onChange={e => changeConfig({...config, add: {...config.add, description: e.target.value}})} name="description" placeholder="Description"/>
                        <button type="submit">Add</button>
                    </form>
                </div>
            </div>)
            // Select
            case 6: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, 5)} className="quit" ><b>X</b></div>
                    <div onClick={e => addTodo(0)} className="chooseCard">Todos<span>({todos.todo.length})</span></div>
                    <div onClick={e => addTodo(1)} className="chooseCard">Doing<span>({todos.doing.length})</span></div>
                    <div onClick={e => addTodo(2)} className="chooseCard">Done<span>({todos.done.length})</span></div>
                </div>
            </div>)
            // Detalles y opciones
            case 7: return (<div className="homeContainer">
                <div className={config.cn}>
                    <div onClick={e => changeScreen(e, config.info.id === 'todo' ? 2 : config.info.id === 'doing' ? 3 : 4)} className="quit" ><b>X</b></div>
                    <h1>{todos[config.info.id][config.info.i]?.title}</h1>
                    <p>{todos[config.info.id][config.info.i]?.description}</p>
                    <div>
                        {config.info.id !== 'todo' && <button onClick={e => handleBtn(0)} className="btnMove">Todo</button>}
                        {config.info.id !== 'doing' && <button onClick={e => handleBtn(1)} className="btnMove">Doing</button>}
                        {config.info.id !== 'done' && <button onClick={e => handleBtn(2)} className="btnMove">Done</button>}
                        <button onClick={e => handleBtn(-1)} className="btnMove">Delete</button>
                    </div>
                </div>
            </div>)

            default: return null;
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);