import { SET_TODO, SET_CONFIG } from "./constants";

const initialState = {
    todos: {
        done: [], // [...localStorage.getItem('Done')],
        doing: [], // [...localStorage.getItem('Doing')],
        todo: [] // [...localStorage.getItem('Todo')]
    },
    config: {
        cn: 'homeCard',
        screen: 0,
        add: {
            title: '',
            description: ''
        },
        info: {id:null}
    }
};

var variable = (state = initialState, action) => {
    switch (action.type) {
        case SET_TODO: {
        //state.formulario = action.form
            return ({...state, todos: action.todos });
        }
        case SET_CONFIG: {
        //state.formulario = action.form
            return ({...state, config: action.config });
        }
        default: return state;
    }
};

export default variable;  